body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fa-bell {
  color: white;
  margin-left: 20px;
}

.fa-envelope{
  color: white;
  margin-left: 20px;
}

.fa-user{
  color: white;
  margin-left: 20px;
}



.autoCompleteInput {
  font: 200 20px/1.5 Helvetica, Verdana, sans-serif;
  border-bottom: 1px solid #ccc;
  border-radius: 5px;
  max-width: 200px;
}

ul.suggestAutocomplete {
  position: absolute; /* относительно чего позиционирование? */
  display: block;
  list-style-type: none;
  margin-top: 30px;
  margin-bottom: 1 em; /* почему em и зачем вообще */
  margin-left: 0;
  margin-right: 0;
  padding-left: 0px; /* повтор */
  top: 10px; /* почему именно это значение.
   выглядит так что значения просто подогнаны так чтобы выглядело как надо */
}


.suggestAutocomplete > li {
  font: 200 20px/1.5 Helvetica, Verdana, sans-serif; /* тут был бы вопрос про inherit */
  border-bottom: 1px solid #ccc;
}

.suggestAutocomplete > li:last-child {
  border: none;
}

.suggestAutocomplete > li {
  text-decoration: none;
  color: #000;
  display: block;
  width: 200px;
  transition: font-size 0.3s ease, background-color 0.3s ease;
}

.suggestAutocomplete > li:hover {
  font-size: 30px;
  background: #FFF7AA;
}

/* что будет с компонентом если его расположить не в начале страницы? */

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

